.Scroll {
	position: absolute;
	width: 19rem;
	top: 70vh;
	right: 23vw;
	filter: none;

	.stripes {
		width: 100%;
	}

	.triangle {
		position: absolute;
		filter: invert(1);
		width: 2.5rem;
		top: 2.1rem;
		left: 15rem;
		transform: rotateX(180deg);
		animation: updown 1s ease-in-out infinite both;
		transform-origin: center center;
	}

	h2 {
		font-family: "Android 101";
		position: absolute;
		top: -6.7rem;
		left: -1.3rem;
		font-size: 2.7rem;
	}
}
@keyframes updown {
	0% {
		transform: rotateX(180deg) translateY(0);
	}
	50% {
		transform: rotateX(180deg) translateY(20%);
	}
	100% {
		transform: rotateX(180deg) translateY(0);
	}
}
